<template>
  <div class="shop" v-bind:style="bgc">
    <h6>{{title}}</h6>
    <div class="pic">

      <img class="shoppic" v-bind:src="shopurl"  height="80px">
    </div>
    <div class="buttonarea">
      <el-button type="primary" round v-on:click="buy('id')">buy</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shopcomponent',
  props: {
    title: String,
    shopurl: String,
    id: String,
    bgc:String,
  },
  methods:{
    buy:function(purchaseid){
      let self = this;
      const axios = require('axios');
      axios.get('/shoppingpage/purchase', {
        params: {
          num:'1',
          id:purchaseid,
        }
      })
      .then(function (response) {
        if(response == -1){
          self.flag = true;
        }
        else{
          self.$router.replace({
          name:"new"
          });
        }
      })
      .catch(function (error) {
        self.flag = true;
      })
      .then(function () {
      });  
    }
  }
}
</script>

